@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:regular,bold,italic&subset=latin,latin-ext);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Pacifico', 'Segoe UI',
    'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
    'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
#root {
  position: absolute;
  height: 100%;
  width: 100%;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.auth-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

body{height:100%}.App{text-align:center;background-color:rgba(81,174,176,0.341176);height:100%}.App-logo{height:5vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 5s linear;animation:App-logo-spin infinite 5s linear}}.App-header{background-color:#282c34;min-height:20vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}@keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}

.main-header{align-items:center;border-bottom:1px solid #bbb;background-color:rgba(81,174,176,0.501961);display:flex;height:100px;padding:0 20px;font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif}.main-header a,.main-header span{font-size:1.2em;margin:0 15px}.main-header>nav{align-items:center;display:flex;flex-grow:1;justify-content:flex-end}@media (max-width: 600px){.main-header{flex-direction:column;font-size:0.7em;height:auto;padding:10px}.main-header h1{font-size:2.5em}}@media (max-width: 450px){.main-header>nav{flex-direction:column;width:100%}.main-header>nav a,.main-header>nav span{border:1px solid #bbb;margin-bottom:5px;text-align:center;width:80%}}

